<template>
  <div id="loop">
    <div class="list" :style="{transform: `translate3d(0, -${curentList.length >= saveCount ? oneHeight * (curentList.length - saveCount) : 0}px, 0)`}">
      <div class="flex-row itemBox hoz-flex-start" ref="inner" v-for="(item, index) in curentList" :key="index">
        <div class="item flex-row">
          <div class="avatar iconSize">
            <img :src="`${item.head}`" onerror='this.src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png"' alt="">
          </div>
          <div>
            <div class="nickname">{{item.name}}</div>
            <div class="text">{{item.minutes}}{{item.recive}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default({
  props: {
    list: {
      type: Array
    }
  },
  data () {
    return {
      curentList: [],
      countObj: {}, // 是否添加过信息
      oneHeight: 0, // 高度
      saveCount: 6 // 显示多少数据
    }
  },
  methods: {
    // 随机生成数据
    ramdom () {
      clearInterval(this.timer)
      this.timer = null
      let flag = true
      let temp = ''
      while (flag) {
        let index = parseInt(Math.random() * this.list.length)
        if (!this.countObj[index]) {
          this.countObj[index] = index
          this.curentList = [...this.curentList, this.list[index]]
          flag = false
        }
      }
      return temp
    },
    // 重新添加一条数据
    countData () {
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
      this.count = parseInt(Math.random() * 570 + 30)
      // this.count = parseInt(Math.random() * 2)
      this.timer = setInterval(() => {
        this.count--
        if (this.count <= 0) {
          this.ramdom()
          clearInterval(this.timer)
          this.timer = null
          if (this.curentList.length >= this.list.length) {
            // this.curentList = []
            this.countObj = {}
          }
          this.countData()
        }
      }, 1000)
    },
    // 获取第一条数据的高度
    getFirstHeight () {
      this.$nextTick(() => {
        this.oneHeight = this.$refs.inner[0].clientHeight
      })
    },
    // 初始化数据
    init () {
      this.curentList = [this.list[0]]
      this.countObj[0] = true
      this.countData()
      this.getFirstHeight()
    }
  },
  mounted () {
    this.init()
    this.saveCount = 5
  }
})
</script>
<style lang="scss" scoped>
#loop{
  position: absolute;
  left: 0;
  width: 75%;
  overflow: hidden;
  top: 0;
  bottom: 0;
  z-index: 200;
  color: #fff;
  .list{
    transition: all 0.4s;
    transform: translate3d(0, 0, 0);
    padding-top: 10px;
  }
  .itemBox{
    height: 100px;
    .item{
      height: 80px;
      padding: 0 40px 0 20px;
      font-size: 20px;
      background: rgba($color: #000000, $alpha: .7);
      border-radius: 0 40px 40px 0;
      white-space: nowrap;
      text-overflow: ellipsis;
      line-height: 1.5;
      overflow: hidden;
      text-align: left;
      .nickname{
        // color: #ff0400;
        margin-right: 10px;
      }
      .avatar{
        width: 48px;
        height: 48px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 10px;
      }
      .text{
        min-width: 0;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        flex: 1;
      }
    }
  }

}
</style>



