r;\n        justify-content: center;\n        font-size: 30px;\n        color: #F5A623;\n        background: rgba($color: #F37F32, $alpha: .1);\n      }\n      .AWidget{\n        width: 300px;\n        height: 56px;\n        line-height: 56px;\n        background: #D0D0D0;\n        display: inline-block;\n        text-align: center;\n        position: relative;\n        border-radius: 8px;\n        margin-left: 30px;\n      }\n      .float-r{\n        float: right;\n        padding: 0 5px;\n        border: 1px solid #ddd;\n      }\n    }\n    .sketch-l-n {\n      margin: 22px 0 15px;\n      padding: 0 30px;\n      display: flex;\n      white-space: nowrap;\n      overflow: hidden;\n      text-overflow: ellipsis;\n      height: 48px;\n      align-items: center;\n      .spanBg{\n        width: 78px;\n        height: 40px;\n        display: inline-block;\n        line-height: 40px;\n        background: #F37F32;\n        border-radius: 8px;\n        color: white;\n        text-align: center;\n        margin-right: 23px;\n      }\n      span:last-child{\n        line-height: 40px;\n        font-weight: 900;\n        max-width: 95%;\n        overflow: hidden;\n        text-overflow: ellipsis;\n      }\n    }\n    .sketch-praise {\n      font-size: 28px;\n      line-height: 40px;\n      padding: 0 30px 24px;\n      .redColor {\n        font-weight: 900\n      }\n    }\n  }\n\n  .referrer_car{\n    margin-top: 20px;\n    margin-bottom: 20px;\n    .referrer_car_i_t{\n      height: 114px;\n      border: 1px solid #EBEAEF;\n      display: flex;\n      flex-flow: row nowrap;\n      align-items: center;\n      justify-content: flex-start;\n      background: #fff;\n      padding: 0 30px;\n      .referrer_car_i_t_img{\n        width: 180px;\n        height: 60px;\n        display: inline-block;\n        box-sizing: border-box;\n        margin-right: 20px;\n        img{\n          width: 100%;\n          height: 100%;\n        }\n      }\n      .referrer_car_i_name{\n        display: inline-block;\n        height: 80px;\n        span{\n          display: block;\n          line-height: 40px;\n        }\n      }\n    }\n    .referrer_car_list{\n      background: white;\n      padding: 38px 30px 30px;\n      .listDate{\n        width: 220px;\n        &:nth-child(3n+2) {\n          margin: 0 14px;\n        }\n        .listDate-img{\n          width: 220px;\n          height: 165.4px;\n          border-radius: 8px;\n          overflow: hidden;\n          display: block;\n          img{\n            width: 100%;\n          }\n        }\n        .listDate-name{\n          margin: 18px 0 10px 0;\n          line-height: 34px;\n        }\n        &:last-child{\n          // margin: 0;\n        }\n      }\n    }\n  }\n\n  .tabType{\n    width: 750px;\n    height: 106px;\n    line-height: 106px;\n    background: white;\n    justify-content: space-between;\n    box-sizing: border-box;\n    div{\n      flex: 1;\n    }\n    span.typeTabIndexActive{\n      color: #F37F32;\n      border-bottom: 3px solid #F37F32;\n      padding-bottom: 30px;\n    }\n  }\n\n  .parameter{\n    background: #fafafa;\n    .parameter-title{\n      box-sizing: border-box;\n      padding-left: 40px;\n    }\n    .parameter-list{\n      box-sizing: border-box;\n      &>div{\n        flex: 1;\n      }\n      :first-child:not(h3){\n        color: #232323;\n        line-height: 1.5\n      }\n    }\n  }\n  .impress{\n    overflow: hidden;\n    font-size: 28px;\n    .imgHeight{\n      max-height: 1000px;\n      overflow: hidden;\n    }\n    &>div:last-child{\n      text-align: center;\n      line-height: 90px;\n      color: #3CA5E9;\n      background: white;\n    }\n  }\n}\n.gradColor{\n  color: #989898;\n}\n",null],"sourceRoot":""}]);

