<template>
  <div class="car-detail">
    <HeaderNavBar v-if="$utils.showNavBar()" :isShopDetail="showDetailValue" @headerShare='headerShare'></HeaderNavBar>
    <div class="slide-car">
      <swiper class="swiper-container" :options="swiperOptions">
        <swiper-slide class="swiper-slide" v-for="(item,index) in sildeIgm" :key="index">
          <img :src="item" onerror="this.src=https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/error.png" alt="">
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
      <!-- <loopList id='loop' :list='loopList' v-if="loopList.length > 0"></loopList> -->
      <div class="openInApp f30 flex-row" @click="openInApp" v-if="$config.source_style < 6">在APP打开></div>
    </div>
    <div class="sketch">
      <div class="sketch-ti flex-row">
        <div class="sketch-left flex-row hoz-flex-start">
          <div class="sketch-left-price flex-row">
            <div class="price">&yen;</div>
            <div class="actrally">0.00</div>
          </div>
          <div class="sketch-left-vcoin">
            <div class="vcoin-top">&yen;{{listDate.vcoin|newMoney}}</div>
            <div class="v-bottom"><div class="redColor">{{listDate.vcoin|newMoney}}个</div>&nbsp;掘金宝兑换</div>
          </div>
        </div>
        <div class="sketch-right">不限量</div>
      </div>
      <div class="sketch-l-n"><span class="f24 spanBg">{{listDate.level}}级</span><span class="f34">{{listDate.fullname}}</span></div>
      <div class="sketch-praise f28 gradColor"><span>{{listDate.sales_num}}人 免费赚到&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;好评率 </span><span class="redColor">{{listDate.eval_rate}}</span></div>
    </div>
    <div class="referrer_car">
      <div class="referrer_car_i_t">
        <span class="referrer_car_i_t_img">
          <img :src="listDate.brand_logo" alt="">
        </span>
        <div class="referrer_car_i_name">
          <span class="f32">{{listDate.brand_name}}</span>
          <span class="f26 gradColor">粉丝人数 {{listDate.shop_fans | setPeople}}</span>
        </div>
      </div>
      <div class="referrer_car_list flex">
        <div class="listDate text-center" v-for="(item,index) in listDate.recommend_goods" :key="index" @click="toMy(item.id)">
          <div class="listDate-img"><img :src="item.thumb" alt=""></div>
          <div class="listDate-name f24">{{item.fullname}}</div>
          <div class="listDate-pr f30 redColor">{{item.price|newMoney}}</div>
        </div>
      </div>
    </div>
    <div></div>
    <div class="tabType flex text-center f36" v-if="showDetail">
      <div  v-for="(item,index) in typeTab" :key="index" @click="toggle_typeTab(index)"><span :class="typeTabIndex===index?'typeTabIndexActive':''">{{item}}</span></div>
    </div>
    <div class="impress" ref="shopContent" :style="{height: contentHeight}" v-if="typeTabIndex===0  && showDetail" >
      <div :class="initImgHeight?'':'imgHeight'" v-html="detail"></div>
      <div @click="initImgHeight=true" v-if="!initImgHeight" class="f24">↓↓↓展开全部</div>
    </div>

    <div class="parameter" :class="{'isHide': isHide}" v-else-if="typeTabIndex===1 && showDetail">
      <div class="parameter-list text-center f24" v-html="specification"></div>
    </div>
    <recommendButtom :listDate="listDate" @setShare='setShare'></recommendButtom>
    <IndexShare @shareSuccess='shareSuccess' :appShare='appShare' v-if="$route.name === 'GoodsDetail'" tag='goods'></IndexShare>
    <ShareCommon v-if="showShare" @closeShare='closeShare'></ShareCommon>
  </div>
</template>

<script>
import HeaderNavBar from '@/components/task/header-nav'
import recommendButtom from '@/components/common/recommend_buttom'
import IndexShare from '@/components/share/IndexShare'
import ShareCommon from '@/components/share/shareCommon'
import loopList from '@/components/personal-center/car_detail/loop_message'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  data () {
    return {
      showDetail: true,
      isHide: false,
      showDetailValue: 1,
      swiperOptions: {
        slidesPerView: 1,
        spaceBetween: 30,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        observer: true,
        observeParents: true,
        initialSlide: 0
      },
      appShare: {
        path: `/share_page/${Math.ceil(3 * Math.random())}`,
        count: 0
      },
      showShare: false,
      contentHeight: '',
      loopList: [],
      typeTab: ['商品详情', '规格参数'],
      typeTabIndex: 0,
      sildeIgm: [],     // 图片
      listDate: {},      // 初始数据
      detail: [],      //
      specification: [],      //
      initImgHeight: true
    }
  },
  mounted () {
    this.getCdata()
    window.addEventListener('scroll', this.setHeader, {passive: false})
  },
  watch: {
    '$route.params.id': function (newId, oldId) {
      if (newId !== oldId) {
        this.showDetail = true
        this.getCdata()
      }
    }
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.setHeader)
  },
  methods: {
    setHeader () {
      if (this.$utils.getScrollTop() > document.querySelector('.slide-car').offsetHeight) {
        this.showDetailValue = 0
      } else {
        this.showDetailValue = 1
      }
      // console.log(this.$utils.getScrollTop())
      // console.log(document.querySelector('.slide-car').offsetHeight)
    },
    getCdata () {
      window.scrollTo(0, 0)
      // 单独对烤箱的标题进行隐藏处理
      if (this.$route.params.id === '1528966665') {
        this.isHide = true
      } else {
        this.isHide = false
      }
      this.$api.get('GOODS_DETAIL', {id: this.$route.params.id}, res => {
        let tempDiv = document.createElement('div')
        tempDiv.innerHTML = res.data.detail
        let divs = tempDiv.querySelectorAll('div')
        for (let div of divs) {
          if (div.innerHTML === '<br>') {
            div.parentNode.removeChild(div)
          }
        }
        res.data.detail = tempDiv.innerHTML
        divs = null
        this.listDate = res.data
        this.sildeIgm = JSON.parse(res.data.pic_list_h)
        if (typeof res.data.detail === 'string') {
          res.data.detail = res.data.detail.replace(this.$reg.BGCHANGE, 'url(http://')
          let div = document.createElement('div')
          div.innerHTML = res.data.detail
          let links = div.getElementsByTagName('a')
          for (let a of links) {
            a.href = 'javascript:;'
            a.title = ''
            let img = a.getElementsByTagName('img')[0]
            if (img) {
              img.alt = ''
              if (!img.src.indexOf()) {
                a.removeChild(img)
              }
            }
          }
          res.data.detail = div.innerHTML
        }
        this.detail = res.data.detail
        this.specification = res.data.specification
        this.setDetailHeight()
        // this.getTopList()
      })
    },
    setDetailHeight () {
      this.$nextTick(() => {
        let viewportWidth = document.documentElement.clientWidth || document.body.clientWidth
        let scale = (viewportWidth / 320 / 2).toFixed(4)
        let rate = ((scale * 2 - 1) * 200).toFixed(2)
        rate = scale * 100
        let contentWidth =  640 * scale
        rate =  ((640 - 640 * scale) / 2 / contentWidth).toFixed(4) * 100
        let detailEl = document.querySelector('.ssd-module-wrap')
        if (detailEl) {
          detailEl.style['transform'] = `scale(${scale}) translate(${-rate}%, ${-rate}%)`
          detailEl.style['WebkitTransform'] = `scale(${scale}) translate(${-rate}%, ${-rate}%)`
          this.contentHeight = detailEl.clientHeight * scale + 'px'
        }
        this.$nextTick(() => {
          let imgs = this.$refs.shopContent.getElementsByTagName('img')
          let flag = true
          if (imgs.length > 0) {
            for (let img of imgs) {
              if (img.getAttribute('src')) {
                flag = false
                break
              } else {
                img.parentNode.removeChild(img)
              }
            }
          } else {
            flag = false
          }
          if (flag) {
            this.showDetail = false
          }
        })
      })
    },
    toggle_typeTab (index) {
      this.typeTabIndex = index
    },
    toMy (id) {
      this.$router.replace({path: '/goods_detail/' + id})
    },
    // 点击头部分享按钮（app分享）
    headerShare () {
      this.appShare.count++
    },
    // 关闭微信分享引导弹窗
    closeShare () {
      this.showShare = false
    },
    // 点击底部分享按钮
    setShare () {
      if (this.$config.source_style >= 6) {
        this.headerShare()
      } else if (this.$utils.isWx()) {
        this.showShare = true
      } else {
        this.$router.push({
          path: '/saveimage',
          query: {
            tag: 'goods'
          }
        })
      }
    },
    // 分享成功操作函数
    shareSuccess () {
      
    },
    // 在app打开
    openInApp () {
      if (this.$config.source_style < 6) {
        if (this.$utils.isWx()) {
          if (this.$utils.isIos()) {
            window.open(this.$config.iOSUrl)
          } else if (this.$utils.isAndroid()) {
            window.open(this.$config.applicationUrl)
          }
        }
      } else {
        this.$utils.openApp({type: 'shops', path: this.$route.path})
      }
    },
    // 获取顶部滚动消息
    getTopList () {
      if (this.loopList.length === 0) {
        this.$api.get('GOODS_RANDOMTOP', {goods_id: this.$route.params.id}, res => {
          this.loopList = res.data
        })
      }
    }
  },
  components: {
    swiper,
    swiperSlide,
    HeaderNavBar,
    recommendButtom,
    IndexShare,
    ShareCommon,
    loopList
  },
  filters: {
    newMoney (num) {
      num = num || 0
      if (num > 9999) {
        let long = length || 2
        return (num / 10000).toFixed(long) + '万'
      } else {
        return num
      }
    }
  }
}
</script>

<style lang="scss"  scoped>
  .car-detail{
    background: #f0f0f0;
    .slide-car{
      height: 562px;
      width: 750px;
      position: relative;
      z-index: 1;
      .swiper-container {
        height: 100%;
      }
      .swiper-pagination {
        bottom: 30px;
      }
      #loop {
        top: 100px;
        bottom: 100px;
      }
      .swiper-slide>img{
        width: 100%;
        height: 100%;
      }
      .openInApp{
        position: absolute;
        right: 0;
        top: 20px;
        height: 70px;
        width: 252px;
        background: #FF7500;
        color: #fff;
        border-radius: 35px 0 0 35px;
        z-index: 1;
      }
      .swiper-wrapper{
        z-index: 0;
      }
    }
    // 中部价格
    .sketch{
      background: white;
      box-sizing: border-box;
      margin: auto;
      .sketch-ti{
        height: 100px;
        .sketch-left {
          width: 580px;
          padding-left: 30px;
          box-sizing: border-box;
          height: 100%;
          background-image: linear-gradient(45deg, #F8994D, #F76B1C);
          .sketch-left-price {
            margin-right: 30px;
            display: flex;
            align-items: flex-end;
            .price {
              font-size: 36px;
              font-weight: 900;
              color: #f00;
              line-height: 44px;
              margin-right: 8px;
            }
            .actrally {
              color: #fff;
              font-weight: 900;
              font-size: 60px;
            }
          }
          .sketch-left-vcoin {
            text-align: left;
            .vcoin-top {
              font-size: 28px;
              text-decoration: line-through;
              color: rgba($color: #fff, $alpha: .5)
            }
            .v-bottom {
              display: flex;
              white-space: nowrap;
              border: 1px solid #fff;
              justify-content: flex-start;
              margin-top: 4px;
              height: 50px;
              box-sizing: border-box;
              line-height: 50px;
              font-weight: 900;
              font-size: 28px;
              padding-right: 20px;
              max-width: 350px;
              text-align: center;
              color: #fff;
              .redColor {
                background: #fff;
                height: 100%;
                padding: 0 20px;
              }
            }
          }
        }
        .sketch-right {
          display: flex;
          width: 180px;
          height: 100%;
          white-space: nowrap;
          align-items: center;
          justify-content: center;
          font-size: 30px;
          color: #F5A623;
          background: rgba($color: #F37F32, $alpha: .1);
        }
        .AWidget{
          width: 300px;
          height: 56px;
          line-height: 56px;
          background: #D0D0D0;
          display: inline-block;
          text-align: center;
          position: relative;
          border-radius: 8px;
          margin-left: 30px;
        }
        .float-r{
          float: right;
          padding: 0 5px;
          border: 1px solid #ddd;
        }
      }
      .sketch-l-n {
        margin: 22px 0 15px;
        padding: 0 30px;
        display: flex;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        height: 48px;
        align-items: center;
        .spanBg{
          width: 78px;
          height: 40px;
          display: inline-block;
          line-height: 40px;
          background: #F37F32;
          border-radius: 8px;
          color: white;
          text-align: center;
          margin-right: 23px;
        }
        span:last-child{
          line-height: 40px;
          font-weight: 900;
          max-width: 95%;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
      .sketch-praise {
        font-size: 28px;
        line-height: 40px;
        padding: 0 30px 24px;
        .redColor {
          font-weight: 900
        }
      }
    }

    .referrer_car{
      margin-top: 20px;
      margin-bottom: 20px;
      .referrer_car_i_t{
        height: 114px;
        border: 1px solid #EBEAEF;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: flex-start;
        background: #fff;
        padding: 0 30px;
        .referrer_car_i_t_img{
          width: 180px;
          height: 60px;
          display: inline-block;
          box-sizing: border-box;
          margin-right: 20px;
          img{
            width: 100%;
            height: 100%;
          }
        }
        .referrer_car_i_name{
          display: inline-block;
          height: 80px;
          span{
            display: block;
            line-height: 40px;
          }
        }
      }
      .referrer_car_list{
        background: white;
        padding: 38px 30px 30px;
        .listDate{
          width: 220px;
          &:nth-child(3n+2) {
            margin: 0 14px;
          }
          .listDate-img{
            width: 220px;
            height: 165.4px;
            border-radius: 8px;
            overflow: hidden;
            display: block;
            img{
              width: 100%;
            }
          }
          .listDate-name{
            margin: 18px 0 10px 0;
            line-height: 34px;
          }
          &:last-child{
            // margin: 0;
          }
        }
      }
    }

    .tabType{
      width: 750px;
      height: 106px;
      line-height: 106px;
      background: white;
      justify-content: space-between;
      box-sizing: border-box;
      div{
        flex: 1;
      }
      span.typeTabIndexActive{
        color: #F37F32;
        border-bottom: 3px solid #F37F32;
        padding-bottom: 30px;
      }
    }

    .parameter{
      background: #fafafa;
      .parameter-title{
        box-sizing: border-box;
        padding-left: 40px;
      }
      .parameter-list{
        box-sizing: border-box;
        &>div{
          flex: 1;
        }
        :first-child:not(h3){
          color: #232323;
          line-height: 1.5
        }
      }
    }
    .impress{
      overflow: hidden;
      font-size: 28px;
      .imgHeight{
        max-height: 1000px;
        overflow: hidden;
      }
      &>div:last-child{
        text-align: center;
        line-height: 90px;
        color: #3CA5E9;
        background: white;
      }
    }
  }
  .gradColor{
    color: #989898;
  }
</style>



